.LoadingTodo-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    height: 30px;
    margin-top: 12px;
    box-shadow: 0px 5px 40px rgba(32, 35, 41, 0.15);
    border-radius: 10px;
    padding: 12px 0;
  }
  
  .LoadingTodo-text {
    margin: 24px 0 24px 24px;
    width: calc(100% - 110px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  
  .LoadingTodo-completeIcon,
  .LoadingTodo-deleteIcon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    height: 40px;
    width: 40px;
  }
  .LoadingTodo-completeIcon {
    position: absolute;
    left: 12px;
  }
  .LoadingTodo-deleteIcon {
    position: absolute;
    top: 5px;
    right: 5px;
  }
  
  
  .LoadingTodo-container,
  .LoadingTodo-completeIcon,
  .LoadingTodo-deleteIcon {
    background: linear-gradient(90deg, rgb(147, 68, 203), rgb(255, 255, 255));
    background-size: 400% 400%;
    animation: loadingAnimation 3s ease-in-out infinite;
  }
  
  @keyframes loadingAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }